html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  user-select: none; }

body {
  margin: 0;
  flex: 1;
  display: flex;
  flex-direction: column; }

.app-container {
  flex: 1;
  display: flex;
  flex-direction: column; }

.contant {
  flex: 1;
  display: flex;
  flex-direction: row;
  background-color: #000;
  color: #fff;
  height: 100%;
  width: 100%; }

.bottom-bar {
  align-items: center;
  background-color: #000;
  color: #fff;
  display: flex;
  flex-direction: row;
  font-family: caption, sans-serif;
  font-size: 12px;
  height: 22px;
  line-height: 13.8px;
  border-top: 0.5px solid #2f2f2f;
  padding: 0 5px 0 5px; }

.left-bar {
  background-color: #000;
  border-right: 0 solid #ccc;
  display: flex;
  flex-direction: column; }

.bar-item {
  width: 50px;
  height: 50px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  background-color: #000;
  color: #fff;
  fill: #fff; }
  .bar-item img {
    height: 100%;
    width: 100%; }

.left-bar .dropdown-menu {
  background: #2b2b2b; }
  .left-bar .dropdown-menu .dropdown-item {
    color: #fff;
    font-size: 14px;
    padding: 5px; }
    .left-bar .dropdown-menu .dropdown-item:focus, .left-bar .dropdown-menu .dropdown-item:hover {
      color: #2b2b2b;
      background-color: #fff; }
  .left-bar .dropdown-menu .dropdown-divider {
    border-top: 1px solid #424242; }

.middle {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 0; }

.middle-top-bar {
  flex-shrink: 0;
  height: 32px;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #000;
  color: #fff;
  padding: 0 6px 0 6px;
  box-sizing: border-box;
  border-bottom: 1px solid #1f1f1f; }
  .middle-top-bar > .sep {
    width: 0;
    margin: 0 10px 0 10px;
    border-left: 1px solid #2f2f2f;
    height: 13px; }
  .middle-top-bar > .item {
    height: 18px;
    line-height: 18px;
    border: 1px solid #2f2f2f;
    outline: 0;
    display: inline-block;
    overflow: hidden;
    box-sizing: border-box;
    padding: 0 3px 0 3px;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    cursor: default;
    text-align: center;
    font-size: 10px; }

.main {
  background: #1f1f1f;
  flex: 1;
  display: flex;
  flex-direction: row; }

#graph, #info {
  display: flex;
  flex-direction: column; }

.gutter.gutter-horizontal {
  background: #2f2f2f;
  cursor: col-resize; }
  .gutter.gutter-horizontal:hover {
    background: #5f5f5f; }

#graph {
  flex: 1;
  position: relative; }

#cy {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 999; }

#info {
  height: calc(100vh - 22px - 32px);
  overflow-y: scroll;
  background: #1f1f1f;
  z-index: 5; }

#information {
  height: auto;
  width: 100%;
  padding: 10px;
  font-size: 12px; }
